<template>
  <header>
    <h2>( Youtube )</h2>
    <p v-if="questionsList2.length>0">
      <span ref="autoplayTips2" class="fw-bold border-bottom"></span>
    </p>
  </header>
  <div class="por w-75" ref="dropWrap2" @click="$goVideoMarkerPosition">

    <!-- Youtube  -->
    <div class="plyr__video-embed" id="player" ref="yt" v-show="questionsList2.length>0">
      <iframe src="https://www.youtube.com/watch?v=B-BQnCwTBeQ?rel=0&amp;showinfo=0?ecver=2"
              allowfullscreen allowtransparency allow="autoplay">
      </iframe>
    </div>

    <!--  -->
    <QuestionsList :dropWrap="dropWrap2" :videoTime="videoTime"></QuestionsList>

    <h3 class="text-danger my-5" v-show="questionsList2.length===0">
      "<router-link to="/teacherQuestions"></router-link>"~
    </h3>
  </div>
</template>

<script>
import Plyr from '../../../../node_modules/plyr'
import { mapState } from 'vuex'

import QuestionsList from '../drop/QuestionsList.vue'
export default {
  components: {
    QuestionsList
  },

  data () {
    return {
      player: '',
      videoTime: 0,
      dropWrap2: '',
      vidStopDisabled: false,
      questionsList: []
    }
  },

  computed: {
    ...mapState(['options', 'questionsList2'])
  },

  mounted () {
    //* 
    if (this.questionsList2.length === 0) return

    //* 
    this.dropWrap2 = this.$refs.dropWrap2
    this.player = new Plyr('#player', {
      // noCookie: false,
      rel: 0, //* 
      // showinfo: 0,
      // iv_load_policy: 3,
      // modestbranding: 1,
      invertTime: false, //* ""
      tooltips: { controls: true, seek: true }, //* icon hover 
      i18n: {
        restart: '',
        rewind: ' {seektime} ',
        fastForward: ' {seektime} ',
        play: '',
        pause: '',
        seek: '',
        played: '',
        buffered: '',
        currentTime: '',
        duration: '',
        volume: '',
        mute: '',
        unmute: '',
        captions: '',
        enableCaptions: '',
        disableCaptions: '',
        enterFullscreen: '',
        exitFullscreen: '',
        frameTitle: '{title} ',
        settings: '',
        speed: '',
        normal: '',
        quality: '',
        loop: '',
        start: '',
        end: '',
        all: '',
        reset: '',
        disabled: '',
        advertisement: ''
      }
    })

    this.questionsList = this.questionsList2

    //* 
    const isAutoplay = this.options.autoplay
    this.$refs.autoplayTips2.textContent = isAutoplay ? '' : ''

    //* 
    setTimeout(() => {
      this.$getMarker()
      this.$hoverMarkerTips()
    }, 1000)

    this.player.on('timeupdate', (event) => {
      //* 
      this.videoTime = this.player.currentTime

      //* 
      this.$questionShowVidStop()
    })
  }
}
</script>

<style lang='scss' scope>
.por {
  position: relative;
}
.marker:hover {
  background-color: blue !important;
}
//*  youtube 
.plyr iframe {
    z-index: 2;
}
</style>
